/* @import url('https://fonts.googleapis.com/css?family=Poppins:100,200,300,400'); */

:root {
  --main-color:#282d33;
  --main-halfopacity: rgba(31, 47, 73, .5);
  
  --lightest-blue: #e9f3ff;
  --lighter-blue:#c7deff;
  --light-blue:#7aa7ee;
  --blue:#5887d3;
  --dark-blue:#375688;

  --about-color:#7aa7ee;
  --portfolio-color: #e07f7f;
  --resume-color:#f7eb80;
  --connect-color:#98db88;
  /* --contact-color:#98db88; */
}

::-moz-selection {
  background: #e9f3ff;
  background: var(--lightest-blue)
}

::selection {
  background: #e9f3ff;
  background: var(--lightest-blue)
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  color: #282d33;
  color: var(--main-color);
  background: #eee;
}

p {
  font-weight: 200;
  margin: 0;
}

h1,h2,h3,h4 {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
  font-weight: 300;
  line-height: 100%; 
}

a {
  -webkit-transition: color 200ms ease;
  transition: color 200ms ease;
  padding: 0;
  color: inherit;
  text-decoration: none;
}

a:hover 
{
  color:inherit; 
  text-decoration:none; 
  cursor:pointer;  
}

ul {
  margin: 0;
  padding: 0;
}

@media (max-width: 950px) {
  #headshot-loader { background: url('/img/photos/headshot-small.jpg')}
}
@media (min-width: 951px) {
  #headshot-loader { background: url('/img/photos/headshot.jpg')}
}


@-webkit-keyframes revealUp {
  from {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes revealUp {
  from {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes revealDown {
  from {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes revealDown {
  from {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes opacity {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes opacity {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

.swipe-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  z-index: 50;
  position: absolute;
}

.swipe-enter.swipe-enter-active {
  z-index: 50;
  position: absolute;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 500ms ease;
  transition: -webkit-transform 500ms ease;
  transition: transform 500ms ease;
  transition: transform 500ms ease, -webkit-transform 500ms ease;
}

.swipe-exit {
  z-index: -50;
  position: absolute;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.swipe-exit.swipe-exit-active {
  z-index: -50;
  position: absolute;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 500ms ease;
  transition: -webkit-transform 500ms ease;
  transition: transform 500ms ease;
  transition: transform 500ms ease, -webkit-transform 500ms ease;
}
